import React, {Component} from 'react';
import {Link, useNavigate} from 'react-router-dom';
import html2canvas from "html2canvas";
import axios from 'axios';
import FormData from 'form-data';

import Match from './Match';

class App extends Component
{
    state = {
        flags: {
            Netherlands: '',
            Iran: '',
            Argentina: '',
            Denmark: '',
            Spain: '',
            Croatia: '',
            Brazil: '',
            Uruguay: '',
            England: '',
            Ecuador: '',
            France: '',
            Mexico: '',
            Belgium: '',
            Germany: '',
            Portugal: '',
            Switzerland: '',
        },
        r16l: {
            1: [
                {name: 'Netherlands', percent: '66.1'},
                {name: 'Iran', percent: '33.9'},
            ],
            2: [
                {name: 'Argentina', percent: '61.1'},
                {name: 'Denmark', percent: '38.9'},
            ],
            3: [
                {name: 'Spain', percent: '57.7'},
                {name: 'Croatia', percent: '42.3'},
            ],
            4: [
                {name: 'Brazil', percent: '65.1'},
                {name: 'Uruguay', percent: '34.9'},
            ],
        },
        r16r: {
            1: [
                {name: 'England', percent: '55.9'},
                {name: 'Ecuador', percent: '44.1'},
            ],
            2: [
                {name: 'France', percent: '61.1'},
                {name: 'Mexico', percent: '38.9'},
            ],
            3: [
                {name: 'Belgium', percent: '57.7'},
                {name: 'Germany', percent: '42.3'},
            ],
            4: [
                {name: 'Portugal', percent: '65.1'},
                {name: 'Switzerland', percent: '34.9'},
            ],
        },
        qfl: {
            1: [
                {name: 'Netherlands', percent: '43.5'},
                {name: 'Argentina', percent: '56.5'},
            ],
            2: [
                {name: 'Spain', percent: '41.5'},
                {name: 'Brazil', percent: '58.5'},
            ],
        },
        qfr: {
            1: [
                {name: 'England', percent: '44.3'},
                {name: 'France', percent: '55.7'},
            ],
            2: [
                {name: 'Belgium', percent: '50.9'},
                {name: 'Portugal', percent: '49.1'},
            ],
        },
        sfl: {
            1: [
                {name: 'Argentina', percent: '48.4'},
                {name: 'Brazil', percent: '51.6'},
            ]
        },
        sfr: {
            1: [
                {name: 'France', percent: '48.9'},
                {name: 'Belgium', percent: '51.1'},
            ]
        },
        final: {
            1: [
                {name: 'Brazil', percent: '61.3'},
                {name: 'Belgium', percent: '38.7'},
            ]
        },
        winner: {name: 'Brazil'},
    }

    exportContent = async () => {
        const fileName = 'World-Cup-2022-Prediction.png'
        const element = document.getElementById('content')
        const canvas = await html2canvas(element)
        const image = canvas.toDataURL("image/png", 1.0)
        // console.log(image)

        const base64Response = await fetch(image)
        const blob = await base64Response.blob()
        const file = this.blobToFile(blob, fileName)

        const formData = new FormData()
        formData.append('file', file, fileName)
        const res = await axios.post('https://tmpfiles.org/api/v1/upload', formData, { headers: {'Content-Type': `multipart/form-data`} })
        const image_url = res.data.data.url
        const image_src = image_url.replace('https://tmpfiles.org', 'https://tmpfiles.org/dl')
        // console.log(image_src)

        // this.downloadImage(image, fileName);
        this.socialShare(image_src)
    }

    blobToFile = (theBlob, fileName) => {
        //A Blob() is almost a File() - it's just missing the two properties below which we will add
        theBlob.lastModifiedDate = new Date();
        theBlob.name = fileName;
        return theBlob;
    }

    socialShare = async (image) => {
        window.FB.getLoginStatus(function (response) {
            console.log(response);
            window.FB.ui({
                display: 'popup',
                method: 'share',
                media: [image],
            }, function(response){});
        })
    }
        
    downloadImage = (blob, fileName) => {
        const fakeLink = window.document.createElement("a");
        fakeLink.style = "display:none;";
        fakeLink.download = fileName;

        fakeLink.href = blob;

        document.body.appendChild(fakeLink);
        fakeLink.click();
        document.body.removeChild(fakeLink);

        fakeLink.remove();
    }
    
    render() {
        var r16l_html = Object.values(this.state.r16l).map((item, index) => {
            return (
                <Match key={`r16l-${index}`} item={item}/>
            )
        })
        var r16r_html = Object.values(this.state.r16r).map((item, index) => {
            return (
                <Match key={`r16r-${index}`} item={item}/>
            )
        })
        var qfl_html = Object.values(this.state.qfl).map((item, index) => {
            return (
                <Match key={`qfl-${index}`} item={item}/>
            )
        })
        var qfr_html = Object.values(this.state.qfr).map((item, index) => {
            return (
                <Match key={`qfr-${index}`} item={item}/>
            )
        })
        var sfl_html = Object.values(this.state.sfl).map((item, index) => {
            return (
                <Match key={`sfl-${index}`} item={item}/>
            )
        })
        var sfr_html = Object.values(this.state.sfr).map((item, index) => {
            return (
                <Match key={`sfr-${index}`} item={item}/>
            )
        })
        var final_html = Object.values(this.state.final).map((item, index) => {
            return (
                <Match key={`final-${index}`} item={item}/>
            )
        })
        return (
            <>
                <h1>World Cup 2022 Prediction</h1>
                <hr/>
                <div className="content" id="content">
                    <div className="match-title">
                        <span>Round 16</span>
                        <span>Quarter Final</span>
                        <span>Semi Final</span>
                        <span>Final</span>
                        <span>Semi Final</span>
                        <span>Quarter Final</span>
                        <span>Round 16</span>
                    </div>
                    <div className="match-content">
                        <div className="r16 left">{r16l_html}</div>
                        <div className="qf left">{qfl_html}</div>
                        <div className="sf left">{sfl_html}</div>
                        <div className="final">
                            <div className="winner">
                                {this.state.flags[this.state.winner.name]}
                            </div>
                            {final_html}
                        </div>
                        <div className="sf right">{sfr_html}</div>
                        <div className="qf right">{qfr_html}</div>
                        <div className="r16 right">{r16r_html}</div>
                    </div>
                </div>
                <div className="social">
                    <button className="btn-download" onClick={e=>this.exportContent()}>Share Image</button>
                </div>
            </>
        )
    }
}

export default function Home(props) {
    return <App {...props} navigate={useNavigate()} />
}